.header{
    background-color: var(--lighter-blue);
    box-shadow: 0 4px 4px 0 #00000040;
}
.header__wrapper{
    max-width: 1316px;
    display: flex;
    margin: 0 auto;
}

.header__item{
    color: white;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    padding: 23px 25px;
    text-decoration: none;
    transition: all .2s ease-in-out;
}
.header__item.active{
    background-color: var(--light-blue);
}
.cats {
    margin-top: 53px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(225px, 1fr));
    grid-gap: 18px;
    gap: 18px;
}

.cat {
    visibility: hidden;
    box-shadow: 0 4px 4px 0 #00000040;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 256px;
    height: 256px;
    transition: all .2s linear;
}

.cat__wrapper {
    visibility: visible;
    position: relative;
    transition: all .2s linear;
}

.cat__image {
    width: 225px;
    height: 225px;
    object-fit: cover;
}

.cat__heart, .cat__heart--on-hover {
    z-index: 1;
    opacity: 0;
    position: absolute;
    bottom: 5px;
    right: 4px;
    transition: all .2s linear;
}

.cat__heart--on-hover{
    display: none;
    z-index: 2;
}

.cat:hover {
    visibility: visible;
}

.cat:hover .cat__heart {
    cursor: pointer;
    opacity: 1;
}

.cat__heart:hover + .cat__heart--on-hover{
    display: block;
    cursor: pointer;
    opacity: 1;
}

.loader{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 300px;
    margin: 51px auto 0;
}

.loader__text{
    font-family: Roboto, sans-serif;
    margin-top: 20px;
    font-size: 20px;
}
*, :before, :after {
    box-sizing: border-box;
}

body {
    margin: 0;
}

#root {
    --lighter-blue: #2196F3;
    --light-blue: #1E88E5;
    --orange: #F24E1E;
    --bright-orange: #FF3A00;
    font-family: "Roboto", sans-serif;
}

.wrapper {
    display: flex;
    flex-direction: column;
    max-width: 1316px;
    margin: 0 auto;
}
.nothing-to-show{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 100px auto 0;
}

@media screen and (max-width: 475px) {
    .wrapper{
        align-items: center;
    }
}
